@import url(http://fonts.googleapis.com/earlyaccess/notosanskr.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.main-header {
  text-align: center;
  margin-top: 6em;
  margin-bottom: 6em;
}
  
.left-info {
  min-width: 20em;
}

.left-info > section {
  width: 50%;
  border-bottom: solid gray 1px;
  padding-bottom: 1em;
  margin-bottom: 1em;
}

.left-info > section > ul {
  font-size: 1.3em;
  line-height: 1.3em;
}

/* links style */
.VisitLink {
    cursor: pointer;
}

.VisitLink:hover {
  color: orange;
}


/* web fonts */

/* custom variables */
:root {
  --deep-orange: #D9493D;
  --soft-orange: #F52423;
}

/* HTML tags */
ul {
  list-style: none;
  padding-left: 0;
}

main > div {
  display: inline;
  float: left;
}

/* classes */
.soft-orange {
  color: #F52423;
  color: var(--soft-orange);
}

.deep-orange {
  color: #D9493D;
  color: var(--deep-orange);
}

.main-ui {
  width: 80%;
  margin: auto;
  font-family: 'Noto Sans KR', sans-serif;
}

